.container {
  width: calc(100% - 50px);
  max-width: 1180px;
  margin: 0 auto;
}

.MainContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 12px;
  position: relative;
  width: 100%;
  padding: 35px;
}

.MProFloat {
  padding: 15px 10px;
  position: fixed;
  bottom: 36px;
  left: calc((100% - 1120px) / 2);
  background-color: rgba(0, 0, 0, 0.8);
  border-radius: 15px;
  z-index: 80;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.20);
}
.MProFloat div {
  gap: 10px;
}

@media (max-width: 1244px) {
  .MProFloat {
    width: 180px;
    left: 60px;
  }
  .MProFloat div {
    width: 100%;
    flex-direction: column;
    gap: 10px;
    align-items: center;
    text-align: center;
    gap: 10px;
  }
}

.outerContainer {
  display: flex;
  justify-content: center;
  position: relative;
  /* 수정됨 */
  max-width: 1180px;
  /* 전체 너비를 1180px로 제한 */
  width: 100%;
}

.MainPostContainer {
  display: flex;
  height: 100%;
  flex-direction: column;
  border: 1px solid var(--disable-gray-3, #C7C7C7);
  width: 681px;
}

.MainLeftContainer {
  width: 230px;
  padding-right: 20px;
  padding-top: 89px;
  display: flex;
  flex-direction: column;
  gap: 70px;
}


.MainRightContainer {
  width: 220px;
  margin-left: 20px;
  display: flex;
  flex-direction: column;
  gap: 32px;
}

/* .PostSection {
  width: 100%;
} */

.HomeButton {
  height: 68px;
  border-bottom: 1px solid var(--disable-gray-3, #C7C7C7);
  display: flex;
  justify-content: center;
  align-items: center;
}

.HomeButton button {
  color: black;
  font-size: 18px;
  font-weight: 700;
  font-family: 'WorkSans';
}

/* MainPostCard */

.PostCardBox {
  padding-top: 40px;
  border-bottom: 1px solid var(--disable-gray-3, #C7C7C7);
}

.Postbar {
  width: 100%;
  display: flex;
  align-items: flex-start;
  flex-wrap: nowrap;
  gap: 47px;
  padding: 0 38px;
  margin-bottom: 55px;
}

.Postbar .PostProfileImg {
  width: 66px;
  height: 66px;
  background-color: #A7A7A7;
  border-radius: 100%;
  border: 1px solid #f1f1f1;
  overflow: hidden;
}

.Postbar .PostProfileImg>img {
  min-width: 66px;
  height: 100%;
}

.PostBar input {
  color: black;
  top: 0;
}

.PostBoxIconButton {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 38px;
  margin-bottom: 55px;
}

.ShowPost {
  height: 64px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-top: 1px solid var(--disable-gray-3, #C7C7C7);
  border-bottom: 1px solid var(--disable-gray-3, #C7C7C7);
}

.ShowPost div {
  color: #E87C3E;
  font-family: Work Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
}

/* Trend */

.TrendContainer {
  /* display: flex;
  justify-content: center;
  flex-direction: column; */
  border: 1px solid var(--disable-gray-3, #C7C7C7);
  border-radius: 10px;
  background: #FBFBFB;
}

.TrendTitle {
  display: flex;
  justify-content: center;
  font-size: 14px;
  font-weight: 700;
  padding-top: 25px;
  padding-bottom: 43px;
}

.TrendContainer .TrendUnits {
  padding-left: 27px;

}

/* user검색 */
.searchUserContainer {
  padding: 10px;
  display: flex;
  flex-direction: column;
  gap: 15px;
  border-radius: 15px;
  border: 1px solid #C7C7C7;
  background: #FBFBFB;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.10);
}
.searchUserTitle {
  padding: 10px 0 0 10px;
  color: #E87C3E;
  font-family: Pretendard;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 0.2px;
}
.searchUser {
  border-top: 1px solid #cccccc;
  padding: 10px;
  max-height: 200px;
  overflow-y: scroll;
}
/*스크롤 커스텀*/
.searchUser::-webkit-scrollbar {
  padding: 10px 0;
  width: 10px;
}
.searchUser::-webkit-scrollbar-thumb {
  height: 30%;
  background: #D9D9D9;
  border-radius: 20px;
  border: 2px solid #fff; 
}
.searchUser::-webkit-scrollbar-track {
  background: none;
}

.searchUser .searchUserResult{
  display: flex;
  flex-direction: column;
  padding: 5px 0;
}
.searchUser .searchUserResult:hover>span {
  color: #E87C3E;
}
.searchUser .searchUserResult>span:last-child {
  color: #C7C7C7;
  font-size: 12px;
}
.searchUser .searchUserResult:hover>span:last-child {
  color: #E87C3E;
}
.searchUser .searchUserResultNone,
.searchUserResultNoinput {
  padding: 20px 0;
  text-align: center;
  color: #C7C7C7;
  font-size: 12px;
}
